<template>
  <v-card class="greeting-card">
    <v-row class="ma-0 pa-0">
      <v-col cols="8">
        <v-card-title class="text-no-wrap pt-1 ps-2">
          Bienvenido
        </v-card-title>
        <v-card-subtitle class="text-no-wrap ps-2">
          {{ username }}
        </v-card-subtitle>
        <v-card-text class="d-flex align-center mt-2 pb-2 ps-2">
          <div>
            <p class="text-xl font-weight-semibold primary--text mb-2">
              {{ hour }}:{{ minute }}:{{ second }}
            </p>

            <v-btn
              small
              color="primary"
              @click="pushUser()"
            >
              Ver Cuenta
            </v-btn>
          </div>
        </v-card-text>
      </v-col>

      <v-col cols="4">
        <v-img
          contain
          height="180"
          width="159"
          :src="require(`@/assets/images/misc/triangle-${$vuetify.theme.dark ? 'dark':'light'}.png`)"
          class="greeting-card-bg"
        ></v-img>
        <v-img
          contain
          height="108"
          max-width="83"
          class="greeting-card-trophy"
          src="@/assets/images/misc/trophy.png"
        ></v-img>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  setup() {
    return {

    }
  },
  data: () => ({
    hour: '',
    minute: '',
    second: '',
    username: '',
  }),
  created() {
    this.reloj()
    this.setInfo()
  },
  methods: {
    setInfo() {
      this.username = sessionStorage.getItem('user')
    },
    reloj() {
      setInterval(() => {
        const time = new Date()
        this.hour = time.getHours()
        this.minute = this.checkSingleDigit(time.getMinutes())
        this.second = this.checkSingleDigit(time.getSeconds())
      }, 1000)
    },
    checkSingleDigit(digit) {
      return (`0${digit}`).slice(-2)
    },
    pushUser() {
      this.$router.push({ name: 'pages-account-settings' })
    },
  },
})
</script>

<style lang="scss" scoped>
.greeting-card {
  position: relative;
  .greeting-card-bg {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  .greeting-card-trophy {
    position: absolute;
    bottom: 10%;
    right: 8%;
  }
}
// rtl
.v-application {
  &.v-application--is-rtl {
    .greeting-card-bg {
      right: initial;
      left: 0;
      transform: rotateY(180deg);
    }
    .greeting-card-trophy {
      left: 8%;
      right: initial;
    }
  }
}
</style>
